*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h1 {
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-size: calc(10px + 8vw);
    font-style: normal;
    font-weight: 700;    
    letter-spacing: 0.02em; 
    position:relative;
}

.profile-holder {
    margin: 3rem;
    position: relative;
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: visible;
}

.profile-pic, .profile-heading{
    object-fit: cover;
    position: absolute;
    bottom: auto;
}

.profile-pic {
    filter: brightness(1.15);
    bottom: 23.88vh;
    height: auto;
    max-height: 50vh;
    margin-left: auto;
    margin-right: auto;
}

.profile-background {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    height: 40%;
    background-color: #f8c6c6;
    z-index: -1;

    display: flex;
    justify-content: center;
    align-items: center;
}

