/* center both headings like the James layout */
.profile-heading {
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    text-align: center;
}

/* white text sits on top, centered */
#first {
    color: #fff;
    z-index: 2;
}

/* pink layer sits lower to create the band effect */
#second {
    color: #f8c6c6;
    top: 50%;
    transform: translate(-50%, 16vh); /* tweak 16vh up/down to taste */
    z-index: 1;
    margin-top: 0;
}
